import { mapState,mapMutations } from 'vuex'
export default {
	data() {
		return {
			height: "100%",
			isDisabled: true,
			mynote: {
				phone: "",
				password: ""
			}
		}
	},
	onLoad() {
		this.height = uni.getSystemInfoSync().screenHeight-67 +"px";
	},
	methods: {
		...mapMutations(['login','changeUserName']),
		go(url) {
			uni.navigateTo({
				url: url
			})
		},
		login1() {
			this.login()
			uni.switchTab({
				url: '../mine/index'
			});
		},
		formSubmit(e) {
			if(this.mynote.phone == "admin" && this.mynote.password == 123456){
				this.login();
				this.changeUserName(this.mynote.phone);
				uni.setStorageSync('info',{userName: this.mynote.phone,password: this.mynote.password});
				uni.switchTab({
					url: '../mine/index'
				});
			}else{
				uni.showToast({
					title: "您输入的账号或者密码不正确",
					icon: 'none'
				})
			}
		},
		verify(e) {
			var id = e.currentTarget.id;
			var ad = this.mynote;
			var _this = this;
			for(var key in ad){
				key == id ? ad[key] = e.detail.value : ad[key];
				if(ad[key] !== ""){
					_this.isDisabled = false;
				}
				for(var k in ad){
					if(ad[k] == ""){
						_this.isDisabled = true
					}
				}
			}
		}
	}
}
